
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  .nav{
    display: flex;
    align-items: center;
    justify-content: space-around;
    height:80px;
    color: #284F9A;
    box-shadow: 0px 0px 5px 0px rgba(141,  141,  141, 0.69);
  }

  .nav_logo{
    cursor: pointer;
  }
  .nav_logo img{
    width: 70px;
    height: 70px;
    margin-right: 40px;
  }
  .nav_title{
    width: 700px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-left: 20px;
  }

  .nav_text{
    transition: 1s;
    font-size: 18px;
  }
  .nav_text:hover{
    color: #A4A6AF;
    transition: 0.3s;
  }

  .nav_rigtht{
    display: flex;
    align-items: center;
    gap: 40px;
  }

  .button__text{
    color: white;
    font-size: 10px;
  }

  .button{
    padding: 10px 15px;
    background-color:  #2D56A5;
    animation: pulse1 2s infinite, pulse2 2s infinite 1s, pulse3 2s infinite 2s;
    cursor: pointer;
  }

 .button_add{
  color: white;
 }

  .phone_num{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
  }

  .phone_num2 {
  display: flex;
  flex-direction: column;
  }
  .phone_num2 a{
    font-size: 18px;
  }

  .login {
    max-width: 45px;
    max-height: 45px;
  }


 

  @keyframes pulse1 {
    0% {
      box-shadow: none;
    }
    50% {
      box-shadow: -7px 0px 0px #0d7be2b6, 7px 0px 0px #0d7be2b6, 0 0 10px rgba(193, 244, 246, 0.698);
    }
    100% {
      box-shadow: none;
    }
  }
  


@media (max-width: 1025px){
  .nav_title{
    padding-left: 0;
  }
  
  .nav_text{
    font-size: 16px;
  }
  .nav_logo img{
    width: 60px;
    height: 60px;
    margin-right: 0px;
  }

  .nav_rigtht{
    margin-right: 20px;
  }

  .phone_num2{
  }
  .phone_num2 a{
    font-size: 14px;
    width: 120px;
  }
  .button
  {
    width: 140px;
    padding: 10px 20px;
  }

  .login{
    max-width: 45px;
    min-height: 45px;
  }
  .phone_num a{
    font-size: 14px;

  }
}
